@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    
    * {
        font-family: "Nunito", "sans-serif";
    }
    
    .banner-image .b-image{
        animation-name: example;
        animation-duration: 2s;
        animation-iteration-count: 1;
    }

    @keyframes example {
        from{
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }

    .home-image-1 {
        background-image: url("./images/gorilla1-op.jpg");
        background-position: center;
        background-size: cover;
        @apply h-96;
    }

    .home-image-2 {
        background-image: url("./images/gorilla2-op.jpg");
        background-position: center;
        background-size: cover;
        @apply h-96;
    }

    .home-image-3 {
        background-image: url("./images/banner-image-op.jpg");
        background-position: center;
        background-size: cover;
        @apply h-96;
    }

    .itenary-1-image {
        background-image: url("./images/hu-chen-op.jpg");
        background-position: center;
        background-size: cover;
    }

    .itenary-2-image {
        background-image: url("./images/banner-image-op.jpg");
        background-position: center;
        background-size: cover;
    }

    .big-image {
        background-image: url("./images/giraffes.jpg");
        background-size: cover;
        background-position: 30%;
    }

    .small-image {
        background-image: url("./images/hu-chen-op.jpg");
        background-size: cover;
    }

    footer {
        
        background-color: #515e4f;
        /* background-image: url("./images/forest.jpg");
        background-size: cover;
        background-position: bottom; */
    }

    .about-banner-image {
        background-image: url('./images/zebras_op.jpg');
        background-size: cover;
        background-position: 50% 5%;
    }

    .destinations-banner-image {
        background-image: url('./images/discover_uganda.jpg');
        background-size: cover;
        background-position: 50% 50%;
    }

    .contact-us-banner-image {
        background-image: url('./images/queen_elizabeth_np.jpg');
        background-size: cover;
        background-position: 10% 60%;
    }

    .font-custom {
        font-family: 'Italiana' !important;
        font-weight: 600;
    }

    .queen_np {
        background-image: url("./images/queen_elizabeth_np-op.jpg");
        background-size: cover;
    }

    .rwanda_np {
        background-image: url("./images/rwanda.jpg");
        background-size: cover;
    }

    .d-bwindi {
        background-image: url("./images/chimp.jpg");
        background-size: cover;
        background-position: center;
    }

    .d-bunyonyi {
        background-image: url("./images/discover_uganda_op.jpg");
        background-size: cover;
        background-position: center;
    }

    .d-murchison {
        background-image: url("./images/murchison-falls-view.jpg");
        background-size: cover;
        background-position: center;
    }

    .d-volcanoes {
        background-image: url("./images/gorilla1-op.jpg");
        background-size: cover;
        background-position: center;
    }

    .d-kivu {
        background-image: url("./images/rwanda.jpg");
        background-size: cover;
        background-position: center;
    }

    .d-rwanda {
        background-image: url("./images/queen_np.jpg");
        background-size: cover;
        background-position: center;
    }

    @media (max-width: 768px) {
        .about-banner-image {
            background-image: url('./images/queen_elizabeth_np-op.jpg');
            background-size: cover;
            background-position: 50% 5%;
        }
    }
}